  font-style: normal;
}

body {
  margin: 0;
  padding: 0;
  font-family: Muli;
  color: #000808;
  font-weight: 400;
}
h1,
h2,
h3,
h4,
h5,
h6,
h7,
p {
  margin: 0;
  padding: 0;
  font-size: inherit;
  font-weight: inherit;
}
.mt-10 {
