/* range pain Slide */

.painIntencty input[type='range'] {
  height: 20px;
  -webkit-appearance: none;
  margin: 0px;
  width: 100%;
}
.painIntencty input[type='range']:focus {
  outline: none;
}
.painIntencty input[type='range']::-webkit-slider-runnable-track {
  width: 100%;
  height: 4px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 0px 0px 0px #000000;
  background: linear-gradient(
    90deg,
    #12c238 0%,
    #72ca2e 18.92%,
    #feca57 38.89%,
    #eeaa52 58.81%,
    #ee5253 78.46%,
    #ee5253 100%
  );
  border-radius: 2px;
  border: 0px solid #000000;
}
.painIntencty input[type='range']::-webkit-slider-thumb {
  box-shadow: 0px 0px 0px #000000;
  border: 2px solid #ffffff;
  height: 12px;
  width: 12px;
  border-radius: 25px;
  background: #000000;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -5px;
}
.painIntencty input[type='range']:focus::-webkit-slider-runnable-track {
  background: linear-gradient(
    90deg,
    #12c238 0%,
    #72ca2e 18.92%,
    #feca57 38.89%,
    #eeaa52 58.81%,
    #ee5253 78.46%,
    #ee5253 100%
