/* range pain Slide */

.painCondition input[type='range'] {
  height: 20px;
  -webkit-appearance: none;
  margin: 0px;
  width: 100%;
}
.painCondition input[type='range']:focus {
  outline: none;
}
.painCondition input[type='range']::-webkit-slider-runnable-track {
  width: 100%;
  height: 4px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 0px 0px 0px #000000;
  background: linear-gradient(
    90deg,
    #12c238 0%,
    #72ca2e 18.92%,
    #feca57 38.89%,
    #eeaa52 58.81%,
    #ee5253 78.46%,
    #ee5253 100%
  );
  border-radius: 2px;
  border: 0px solid #000000;
}
.painCondition input[type='range']::-webkit-slider-thumb {
  box-shadow: 0px 0px 0px #000000;
  border: 2px solid #ffffff;
  height: 12px;
  width: 12px;
  border-radius: 25px;
  background: #000000;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -5px;
}
.painCondition input[type='range']:focus::-webkit-slider-runnable-track {
  background: linear-gradient(
    90deg,
    #12c238 0%,
    #72ca2e 18.92%,
    #feca57 38.89%,
    #eeaa52 58.81%,
    #ee5253 78.46%,
    #ee5253 100%
  );
}
.painCondition input[type='range']::-moz-range-track {
  width: 100%;
  height: 4px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 0px 0px 0px #000000;
  background: linear-gradient(
    90deg,
    #12c238 0%,
    #72ca2e 18.92%,
    #feca57 38.89%,
    #eeaa52 58.81%,
    #ee5253 78.46%,
    #ee5253 100%
  );
  border-radius: 2px;
  border: 0px solid #000000;
}
.painCondition input[type='range']::-moz-range-thumb {
  box-shadow: 0px 0px 0px #000000;
  border: 2px solid #ffffff;
  height: 12px;
  width: 12px;
  border-radius: 25px;
  background: #000000;
  cursor: pointer;
}
.painCondition input[type='range']::-ms-track {
  width: 100%;
  height: 4px;
  cursor: pointer;
  animate: 0.2s;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
.painCondition input[type='range']::-ms-fill-lower {
  background: linear-gradient(
    90deg,
    #12c238 0%,
    #72ca2e 18.92%,
    #feca57 38.89%,
    #eeaa52 58.81%,
    #ee5253 78.46%,
    #ee5253 100%
  );
  border: 0px solid #000000;
  border-radius: 4px;
  box-shadow: 0px 0px 0px #000000;
}
.painCondition input[type='range']::-ms-fill-upper {
  background: linear-gradient(
    90deg,
    #12c238 0%,
    #72ca2e 18.92%,
    #feca57 38.89%,
    #eeaa52 58.81%,
    #ee5253 78.46%,
    #ee5253 100%
  );
  border: 0px solid #000000;
  border-radius: 4px;
  box-shadow: 0px 0px 0px #000000;
}
.painCondition input[type='range']::-ms-thumb {
  margin-top: 1px;
  box-shadow: 0px 0px 0px #000000;
  border: 2px solid #ffffff;
  height: 12px;
  width: 12px;
  border-radius: 25px;
  background: #000000;
  cursor: pointer;
}
.painCondition input[type='range']:focus::-ms-fill-lower {
  background: linear-gradient(
    90deg,
    #12c238 0%,
    #72ca2e 18.92%,
    #feca57 38.89%,
    #eeaa52 58.81%,
    #ee5253 78.46%,
    #ee5253 100%
  );
}
.painCondition input[type='range']:focus::-ms-fill-upper {
  background: linear-gradient(
