  width: 20px;
}
.roomsNum3 ul li:first-child::after {
  content: "";
  background-image: url(data:image/png;base64,);
  background-size: contain;
  height: 12px;
  width: 9px;
  position: absolute;
  top: 0px;
  left: auto !important;
  left: initial !important;
  right: 6px !important;
  background-repeat: no-repeat;
}
.roomsNum3 ul li:last-child {
  margin-bottom: 0px;
  font-size: 12px;
}
.roomsNum3 ul li:last-child::before {
  border-left: none;
  height: auto;
  height: initial;
  content: "";
}
.roomsNum3 ul li:nth-last-child(2)::before {
  height: auto;
  height: initial;
  border-left: none;
}
.roomsNum3 ul li::before {
  content: "";
  height: 42px;
  width: 10px;
  position: absolute;
  top: 32px;
  left: 8px;
  background-repeat: no-repeat;
  color: #d8d8d8;
  border-left: 1px solid #d8d8d8;
}
.roomsNum3 ul li::after {
  content: "-";
  position: absolute;
  top: 0px;
  left: 8px;
  background-repeat: no-repeat;
  color: #d8d8d8;
}
